import supabase from "../config/supabaseClient"
import { useState,useEffect } from "react"

import SmoothieCard from "../components/SmoothieCard"

const Home = ()=>{
    //   
    const [fetchError, setFetchError]=useState(null)
    //supabase   
    const [smoothies,setSmoothies]=useState(null)
    //
    const [orderBy, setOrderBy]=useState('created_at')

    // 
    const handleDelete = (id) =>{
        setSmoothies(pervSmoothies=>{
            return pervSmoothies.filter(sm=>sm.id!==id)
        })
    }

    useEffect(()=>{
        const fetchSmoothies = async()=>{ //supabase
            const {data,error} = await supabase
            .from('smoothies')
            .select()
            .order(orderBy,{ascending:false});
            if(error){
                setFetchError('Could not fetch the smoothies');
                setSmoothies(null);
            }
            if(data){
                setSmoothies(data);
                setFetchError(null);
            }
        }
        fetchSmoothies();
    },[orderBy]);

    return(
        <div className="page home">
            {fetchError&&(<p>{fetchError}</p>)}
            {smoothies&&(
                <div className="smoothies">
                    <div className="order-by">
                        <p>Order by:</p>
                        <button onClick={()=>setOrderBy('created_at')}>Time Create</button>
                        <button onClick={()=>setOrderBy('title')}>title</button>
                        <button onClick={()=>setOrderBy('rating')}>rating</button>
                    </div>
                    <div className="smoothie-grid">
                        {
                            smoothies.map(smoothie=>(
                                // <div key={smoothie.id} className="smoothie-card">
                                //     <h3>{smoothie.title}</h3>
                                //     <p>Rating: {smoothie.rating}</p>
                                //     <p>{smoothie.method}</p>
                                // </div>
                                <SmoothieCard key={smoothie.id} smoothie={smoothie} onDelete={handleDelete} />
                            ))
                        }
                    </div>
                </div>
            )}
        </div>
    )
}

export default Home;